body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  font-family: 'Roboto', 'sans-serif';
  background: url('http://img1.wsimg.com/isteam/stock/29950');
  overflow: hidden;
}

.text--bold {
  font-weight: bold;
}

.grid .column {
  cursor: pointer;
}

.weather {
  width: 82%;
  height: 150px;
  border-radius: 100%;
  margin-bottom: 20px;
}

.sun {
  background: url('https://i2.wp.com/www.thegeniusmedia.com.ng/wp-content/uploads/2018/03/Sunny_Weather.jpg?fit=600%2C375&ssl=1');
  background-size: cover;
  background-position: center;
}

.rain {
  background: url('https://kurir.mk/wp-content/uploads/2018/03/dozd3.jpg');
  background-size: cover;
  background-position: center;
}

.snow {
  background: url('https://images.mentalfloss.com/sites/default/files/styles/mf_image_16x9/public/520560-istock-614704346.jpg?itok=8cYvvmg5&resize=1100x1100');
  background-size: cover;
  background-position: center;
}

.cloud {
  background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR4ummb2kMpqu-pJXLsALru01MtFBP4U6SrQCQT_3KSmbt2lRys8Q');
  background-size: cover;
  background-position: center;
}

.holder {
    margin-top: 20px !important;
    min-height: 50vh;
    max-height: 75vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.holder div {
    margin-top: 3px;
}

.holder-info {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.holder-info .column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    min-width: 130px;
    margin-top: 3px;
}

.more-info-holder {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    margin: 33px;
    border: 1px solid #737373;
}

